.content-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.video-player {
    /* Add your specific styles for the video player section here */
    width: 100%;
    height: 100%;
    background-color: #000;
    color: #fff;
}

.episode-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: flex-start;
}

.episode-list-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    max-height: 400px;
}
.episode-button {
    background-color: transparent;
    border: none;
    padding: 5px 15px;
    margin: 5px;
    cursor: pointer;
    font-weight: bold;
    border-radius: 4px;
}

.episode-button:hover {
    background-color: #3498db;
    color: #fff;
}
.episode-button.selected {
    background-color: #3498db;
    color: #fff;
}

.episode-button.selected:hover {
    background-color: #3498db;
    color: #fff;
}