body {
  font-family: 'Arial', sans-serif;
  background-color: #1c1c1c;
  color: #fff;
  margin: 0;
  padding: 0;
}

.App {
  max-width: 1200px;
  margin: 0 auto;
}

.content-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

h1 {
  color: #f39c12;
}

.search-bar {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

input {
  padding: 10px;
  font-size: 16px;
}

button {
  padding: 10px;
  font-size: 16px;
  background-color: #3498db;
  color: #fff;
  border: none;
  cursor: pointer;
}

.search-results {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.search-results-item {
  background-color: #2c3e50;
  padding: 20px;
  margin: 10px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  width: 300px;
}

.search-results-item img {
  max-width: 100%;
  height: auto;
  border-radius: 5px;
}

.search-results-item h2 {
  margin-bottom: 10px;
  color: #f39c12;
}

.search-results-item p {
  color: #ecf0f1;
}

.select-anime-btn {
  background-color: #2ecc71;
  color: #fff;
  border: none;
  padding: 10px;
  cursor: pointer;
}
@media only screen and (max-width: 600px) {
  .content-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .video-player {
    width: 100%;
    margin-top: 20px;
  }

  .episode-list-container {
    margin-top: 20px;
  }

  .episode-button {
    width: calc(20% - 8px); /* Adjust width based on your preference */
    margin-right: 8px;
    margin-bottom: 10px;
  }
  
  .episode-list-container .episode-button:nth-child(5n) {
    margin-right: 0;
  }
}